import Vue from "vue"
import VueRouter from "vue-router"

Vue.use(VueRouter)
const routes = [
    {
        path: '/',
        redirect: '/lobby/splash',
    },
    {
        path: '/lobby/splash',
        component: () => import('@/views/lobby/Splash'),
    },
    {
        path: '/lobby/ConstructionInformation',
        component: () => import('@/views/lobby/ConstructionInformation'),
    },
    {
        path: '/TBM/StartGuide',
        component: () => import('@/views/TBM/StartGuide'),
    },
    {
        path: '/TBM/Step01',
        component: () => import('@/views/TBM/Step01'),
    },
    {
        path: '/TBM/Step02',
        component: () => import('@/views/TBM/Step02'),
    },
    {
        path: '/TBM/Step03',
        component: () => import('@/views/TBM/Step03'),
    },
    {
        path: '/TBM/Step04',
        component: () => import('@/views/TBM/Step04'),
    },
    {
        path: '/TBM/Step05',
        component: () => import('@/views/TBM/Step05'),
    },
    {
        path: '/TBM/Step06',
        component: () => import('@/views/TBM/Step06'),
    },
]
const router = new VueRouter({
    mode: "history",
    base: '/',
    routes,
    scrollBehavior (to, from, savedPosition) {
        return { x: 0, y: 0 }
    }
})

export default router
