<template>
    <v-app-bar class="wrap" flat style="height: 55px">
        <div class="appbar">
                <div>
                    <v-btn v-if="left" icon @click="onClickLeft">
                        <img
                            draggable="false"
                            :src="leftIcon"
                            transition="scale-transition"
                            class="appbar-btn"
                        />
                    </v-btn>
                    <div v-else class="appbar-box"></div>
                </div>
                <div class="appbar-title">
                    {{ title }}
                </div>
                <div> 
                    <v-btn v-if="right" icon @click="onClickRight">
                        <img        
                            draggable="false"                
                            :src="rightIcon"
                            transition="scale-transition"
                            class="appbar-btn"
                        />
                    </v-btn>                    
                    <div v-else class="appbar-box"></div>
                </div>
        </div>
    </v-app-bar>
</template>

<script>
export default {
    name: 'AppBar',
    props: [ 'title', 'left', 'right' ],
    data() {
        return {
            leftIcon: require('@/assets/images/icon/i_backarrow.png'),
            rightIcon: require('@/assets/images/icon/i_Header.png')
        }
    },
    methods: {
        onClickLeft() {
            return this.$router.go(-1)
        },
        onClickRight() {
            return this.$router.replace('/lobby/splash')
        }
    }
}
</script>

<style scoped lang="scss">
    ::v-deep .v-toolbar__content {
        height: 57px;
        padding: 0px;
        background-color: #333950;
    }
    .wrap {
        position: fixed; 
        top: 0; 
        z-index: 10;
    }
    .appbar {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #333950;
        height: 52px;
        padding: 0px 8px;

        &-box {
            width: 48px;
            height: 48px;
        }
        &-btn {
            width: 24px;
            height: 24px;
        }
        &-title {
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            text-align: center;
        }
    }
    ::v-deep .theme--light.v-btn--active::before {
        opacity: 0;
    }
    ::v-deep .theme--light.v-btn.v-btn--icon::before {
        color: white;
    }

</style>